import React, { useState } from "react";
import {
  CardTitle,
  CardText,
  Row,
  Col,
  Button,
  Card,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
} from "reactstrap";
import "../App.css";
import { useNavigate } from "react-router-dom";
import { getInterpolation } from "./function.js";
import logo from '../logo.svg';
import '../App.css';
import { Collapse } from 'reactstrap';



function Ex5() {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  const navigate = useNavigate();
  const [x, setx] = useState('5');
  const [xi1, setxi1] = useState('0');
  const [xi2, setxi2] = useState('8');
  const [xi3, setxi3] = useState('14');
  const [xi4, setxi4] = useState('20');
  const [xi5, setxi5] = useState('23');
  const [xi6, setxi6] = useState('30');
  const [yi1, setyi1] = useState('0');
  const [yi2, setyi2] = useState('50');
  const [yi3, setyi3] = useState('96');
  const [yi4, setyi4] = useState('110');
  const [yi5, setyi5] = useState('100');
  const [yi6, setyi6] = useState('86');

  const [result, setresult] = useState();

  const back = () => {
    navigate("/");
  };

  const generate = () => {
    const data = {
      x: x,
      xi: [xi1, xi2, xi3, xi4, xi5, xi6],
      yi: [yi1, yi2, yi3, yi4, yi5, yi6]

    };
    getInterpolation(data).then((res) => {
      setresult(`ด้วยเวลา ${x} วินาที / ความเร็วโดยประมาณจะอยู่ที่ ${res} `)
    });
  };
  return (
    <div className="App-header">

      <div className="text-center" >
        <img src={logo} className="App-logo" alt="logo" />
        <h1 style={{ marginTop: "5px", marginRight: "20px", marginLeft: "20px" }}>
        </h1>
      </div>
      <Button color="primary" onClick={toggle} style={{ marginBottom: '1rem'}}>Link Start</Button>
      <Collapse isOpen={isOpen}>
        <div className="text-center">
          <h1 style={{ marginTop: "20px" }}>Interpolation</h1>
        </div>

        <div className="text-center">
          <p>TestSystem :</p>
          <p>T = 5</p>
          <p>x = 0, 8, 14, 20, 23, 30 ]</p>
          <p>y = [ 0, 50, 96, 110, 100, 86 </p>
        </div>

        <div class="container" className="text center">
          <Row>

            <Col>
              <h3 style={{ marginTop: "20px", marginLeft: "20px" }}> T </h3>
              <Input
                style={{
                  marginLeft: "20px",
                  marginTop: "20px",
                  marginBottom: "20px",
                  width: 60,
                }}
                value={x}
                onChange={(e) => setx(e.target.value)}
                type="text"
                name="bit2string"
                id="bit2string"
              /> </Col>
          </Row>
          {/* .........................................................................         */}
          <Row>
            <Col>
              <h3 style={{ marginTop: "20px", marginLeft: "20px" }}> X </h3>
            </Col>
          </Row>

          <Row>
            <Col>
              <Input
                style={{
                  marginLeft: "20px",
                  marginTop: "20px",
                  marginBottom: "20px",
                  width: 60,
                }}
                value={xi1}
                onChange={(e) => setxi1(e.target.value)}
                type="text"
                name="bit2string"
                id="bit2string"
              /></Col>
            <Col>
              <Input
                style={{
                  marginLeft: "20px",
                  marginTop: "20px",
                  marginBottom: "20px",
                  width: 60,
                }}
                value={xi2}
                onChange={(e) => setxi2(e.target.value)}
                type="text"
                name="bit2string"
                id="bit2string"
              />
            </Col>
            <Col>
              <Input
                style={{
                  marginLeft: "20px",
                  marginTop: "20px",
                  marginBottom: "20px",
                  width: 60,
                }}
                value={xi3}
                onChange={(e) => setxi3(e.target.value)}
                type="text"
                name="bit2string"
                id="bit2string"
              /></Col>
          </Row>
          <Row>
            <Col><Input
              style={{
                marginLeft: "20px",
                marginTop: "20px",
                marginBottom: "20px",
                width: 60,
              }}
              value={xi4}
              onChange={(e) => setxi4(e.target.value)}
              type="text"
              name="bit2string"
              id="bit2string"
            /></Col>
            <Col><Input
              style={{
                marginLeft: "20px",
                marginTop: "20px",
                marginBottom: "20px",
                width: 60,
              }}
              value={xi5}
              onChange={(e) => setxi5(e.target.value)}
              type="text"
              name="bit2string"
              id="bit2string"
            /></Col>
            <Col><Input
              style={{
                marginLeft: "20px",
                marginTop: "20px",
                marginBottom: "20px",
                width: 60,
              }}
              value={xi6}
              onChange={(e) => setxi6(e.target.value)}
              type="text"
              name="bit2string"
              id="bit2string"
            />
            </Col>
          </Row>
          {/* .........................................................................  */}
          <Row>
            <Col>
              <h3 style={{ marginTop: "20px", marginLeft: "20px" }}> Y </h3>
            </Col>
          </Row>

          <Row >
            <Col>
              <Input
                style={{
                  marginLeft: "20px",
                  marginTop: "20px",
                  marginBottom: "20px",
                  width: 60,
                }}
                value={yi1}
                onChange={(e) => setyi1(e.target.value)}
                type="text"
                name="bit2string"
                id="bit2string"
              /></Col>
            <Col>
              <Input
                style={{
                  marginLeft: "20px",
                  marginTop: "20px",
                  marginBottom: "20px",
                  width: 60,
                }}
                value={yi2}
                onChange={(e) => setyi2(e.target.value)}
                type="text"
                name="bit2string"
                id="bit2string"
              /></Col>
            <Col>
              <Input
                style={{
                  marginLeft: "20px",
                  marginTop: "20px",
                  marginBottom: "20px",
                  width: 60,
                }}
                value={yi3}
                onChange={(e) => setyi3(e.target.value)}
                type="text"
                name="bit2string"
                id="bit2string"
              /></Col>
          </Row>
          <Row>
            <Col>
              <Input
                style={{
                  marginLeft: "20px",
                  marginTop: "20px",
                  marginBottom: "20px",
                  width: 60,
                }}
                value={yi4}
                onChange={(e) => setyi4(e.target.value)}
                type="text"
                name="bit2string"
                id="bit2string"
              /></Col>
            <Col><Input
              style={{
                marginLeft: "20px",
                marginTop: "20px",
                marginBottom: "20px",
                width: 60,
              }}
              value={yi5}
              onChange={(e) => setyi5(e.target.value)}
              type="text"
              name="bit2string"
              id="bit2string"
            /></Col>
            <Col>
              <Input
                style={{
                  marginLeft: "20px",
                  marginTop: "20px",
                  marginBottom: "20px",
                  width: 60,
                }}
                value={yi6}
                onChange={(e) => setyi6(e.target.value)}
                type="text"
                name="bit2string"
                id="bit2string"
              />
            </Col>
          </Row>
        </div>
        <h3 style={{ marginBottom: "10px" }}>Answer : {result}</h3>
        <div>
          <Button onClick={generate} color="primary" style={{ marginRight: "100px" }} >
            <div style={{ width: 150, height: 25 }}>
              Commit
            </div>
          </Button>

          <Button onClick={back} color="secondary" style={{ marginLeft: "435px" }}>
            <div style={{ width: 180, height: 25 }}>
              <h5> Back to menu </h5>
            </div>
          </Button>
        </div>
      </Collapse>
    </div >

  );
}
export default Ex5;